<template>
  <!-- 添加产品 -->
  <div>
    <el-dialog v-model="dialogVisible" title="添加产品" width="1000px">
      <div class="follow_con_box">
        <el-input
          style="width: 250px"
          size="mini"
          v-model="dataobj.keyword"
          placeholder="编码/缩写/名称"
        ></el-input>
        <el-select
          style="margin-left: 10px"
          class="top_sel"
          size="mini"
          v-model="dataobj.produce_boss"
          placeholder="生产厂家"
        >
          <el-option
            v-for="item in produce_bosslist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <el-select
          style="margin-left: 10px"
          class="top_sel"
          size="mini"
          v-model="dataobj.producetype"
          placeholder="产品分类"
        >
          <el-option
            v-for="item in producetypelist"
            :key="item.value2"
            :label="item.label"
            :value="item.value2"
          >
          </el-option>
        </el-select>

        <el-button
          style="margin-left: 10px"
          class="search_btn"
          type="primary"
          size="mini"
          @click="search()"
          >查 询</el-button
        >
        <el-button size="mini" @click="resetForm()" style="margin-left: 10px"
          >重 置</el-button
        >
      </div>

      <div class="follow_table">
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          :height="$store.state.innerheight - 340"
          :header-cell-style="{ background: '#f5f5f5' }"
          size="mini"
        >
          <el-table-column type="selection" width="55" fixed />
          <el-table-column prop="pro_abbreviation" label="产品缩写" />
          <el-table-column prop="pro_name" label="产品名称" width="110" />
          <el-table-column prop="model" label="规格型号" width="110" />
          <el-table-column prop="Company" label="单位">
            <template #default>
              <div>
                <el-input size="mini" v-model="number"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="number" label="数量">
            <template #default>
              <div>
                <el-input size="mini" v-model="number"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="order_price" label="订单单价">
            <template #default>
              <div>
                <el-input size="mini" v-model="number"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="order_num" label="订单小计" />
          <el-table-column prop="stand_price" label="标准单价" />
          <el-table-column prop="pro_busness" label="生产厂商" width="110" />
          <el-table-column prop="produce" label="货号" width="110" />
          <el-table-column prop="regesiter" label="注册证号" width="110" />
          <el-table-column
            prop="regesiter_inv"
            label="注册证有效期"
            width="110"
          />
          <el-table-column prop="pro_code" label="产品编码" width="110" />

          <el-table-column prop="pro_type" label="产品分类" />
        </el-table>
      </div>
      <div class="footer_box" style="margin-top: 5px">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>

      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false" size="mini"
            >确定</el-button
          >
          <el-button @click="dialogVisible = false" size="mini">取消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      dialogVisible: false,
      keyword: "",
      produce_boss: "",
      produce_bosslist: [],
      producetype: "",
      producetypelist: [],
      tableData: [
        {
          pro_abbreviation: "1",
          pro_name: "1",
          model: "1",
          pro_boss: "1",
          Company: "1",
          number: "1",
          order_price: "1",
          order_num: "1",
          stand_price: "1",
          pro_busness: "d",
          pro_code: "1111",
          pro_type: "1",
        },
      ],
      dataobj: {},
    });
    const newshow = () => {
      proxy.dialogVisible = true;
    };
    return {
      ...toRefs(state),
      newshow,
    };
  },
};
</script>

<style scoped>
.follow_table {
  margin-top: 10px;
}
</style>
<style>
.el-dialog {
  display: flex;
  flex-direction: column;
  margin: 0 !important;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*height:600px;*/
  max-height: calc(100% - 30px);
  max-width: calc(100% - 30px);
}
.el-dialog .el-dialog__body {
  flex: 1;
  overflow: auto;
}
</style>