<template>
 <!-- 点击新建进入合同管理新建界面 -->
    <div class="con_box">
         <el-tabs class="eltabs" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane class="eltabpane" label="合同信息" name="contractdetail">
                <newinfo ref="newinfo"></newinfo>
            </el-tab-pane>
        </el-tabs>
        <div class="return_btn">
            <el-button v-if="activeName=='contractdetail'" size="mini" type="primary">保存</el-button>
            <el-button @click="returnrouter" size="mini">返回</el-button>
        </div>
    </div>
</template>

<script>
import { reactive, toRefs ,getCurrentInstance,onMounted} from 'vue'
import newinfo from './contractdetail.vue'
export default {
    components:{newinfo},
    setup () {
      const { proxy } = getCurrentInstance()
      console.log('11111111111')
        console.log(proxy.$route)
        const state = reactive({
            activeName:'contractdetail',
            isedit:null
        })
        const handleClick = (tab,event) =>{
            console.log(tab)
            console.log(event)
        }
        const returnrouter = () => {
            proxy.$store.state.isedit = 1
            proxy.$router.go(-1)
        }
        onMounted(()=>{
            proxy.isedit = proxy.$route.query.isedit
        })
    
        return {
            handleClick,
            ...toRefs(state),
            returnrouter,
        }
    }
}
</script>

<style scoped>
.con_box{
        background: #fff;
        height: 100%;
        position: relative;
    }
    .return_btn{
        position: absolute;
        right: 10px;
        top: 5px;
    }
</style>
<style>
    .eltabs.el-tabs .el-tabs__nav-scroll{
        padding: 0 10px;
        box-sizing: border-box;
    }
    .eltabs.el-tabs .el-tabs__content{
        padding: 0 10px;
        padding-top: 10px;
        box-sizing: border-box;
        background: #fff;
    }
</style>